import React, { useState, useEffect } from 'react'
import axios from 'axios'
// Layouts
import Layout from '../layouts/Layout'
// Components
import CreateSteps from '../components/Create/CreateSteps'
import SetupReward from '../components/Create/SetupReward'
// Constants
import { NULL_ADDRESS } from '../utils/constants'

export const STEPS = {
  REWARD_SETUP: 'REWARD_SETUP',
  REQUIREMENTS_SETUP: 'REQUIREMENTS_SETUP',
  DEPLOYMENT: 'DEPLOYMENT',
}

const CreatePage = (props) => {
  // Static data
  const [assets, setAssets] = useState([])
  const [contracts, setContracts] = useState({})
  // Form data
  const [step, setStep] = useState(STEPS.REWARD_SETUP)
  const [form, setForm] = useState({
    reward: {},
    requirements: [],
  })

  useEffect(() => {
    axios.get(`http://35.229.209.76/api/v1/contracts?limit=300&offset=0`)
      .then(({ data }) => {
        setContracts(data.contracts.reduce((p, { contractAddress, ...c }) => {
          // eslint-disable-next-line no-param-reassign
          p[contractAddress] = c
          return p
        }, {}))
      })
      .catch(console.error) // eslint-disable-line no-console
  }, [0])
  
  useEffect(() => {
    if (props.selectedAddress !== NULL_ADDRESS) {
      axios.get(`http://35.229.209.76/api/v1/assets?ownerAddress=${props.selectedAddress}`)
        .then(({ data }) => { setAssets(data.assets) })
        .catch(console.error) // eslint-disable-line no-console
    }
  }, [props.selectedAddress])

  return <Layout {...props}>
    <CreateSteps step={step} />
    <SetupReward assets={assets} contracts={contracts} />
  </Layout>
}

export default CreatePage